// middleware.ts
import { NextResponse } from 'next/server';
import type { NextRequest } from 'next/server';
import { useAuth } from './context/AuthContext';
import Cookies from 'js-cookie';
export async function middleware(request: NextRequest) {
  const path = request.nextUrl.pathname;
  const { isAuthenticated , userRole} = useAuth();
  // General pages that the user should not visit if they are logged in.
  const publicAuthPages = [
    '/auth/login',
    '/auth/signup',
    '/auth/signup/host',
    '/auth/signup/user',
  ];

  // Protected pathways by role
  const protectedPaths = [
    { path: '/admin', role: 'admin' },
    { path: '/host', role: 'host' },
    { path: '/user', role: 'user' },
    { path: '/profile', role: ['user', 'admin', 'host'] },
  ];

  // Check if the path is protected.
  const protectedPath = protectedPaths.find((p) => path.startsWith(p.path));

  const tokent = Cookies.get("token");
  console.log("token", tokent)
  // âœ… ØªØ­Ù‚Ù‚ Ù…Ù† Ø­Ø§Ù„Ø© ØªØ³Ø¬ÙŠÙ„ Ø§Ù„Ø¯Ø®ÙˆÙ„ Ø¨Ø§Ø³ØªØ®Ø¯Ø§Ù… API Ù…Ø­Ù…ÙŠØ©

  // âœ… Ø§Ù„Ù…Ø³ØªØ®Ø¯Ù… Ù…Ø³Ø¬Ù„ Ø¯Ø®ÙˆÙ„ØŒ Ù„Ø§ ØªØ¯Ø¹Ù‡ ÙŠØ¯Ø®Ù„ ØµÙØ­Ø§Øª auth
  if (publicAuthPages.some((p) => path.startsWith(p))) {
    // Redirect by role
    if (isAuthenticated && userRole) {
      if (userRole === 'admin') {
        return NextResponse.redirect(new URL('/admin/dashboard', request.url));
      } else if (userRole === 'host') {
        return NextResponse.redirect(new URL('/host/dashboard', request.url));
      } else if (userRole === 'user') {
        return NextResponse.redirect(new URL('/user/dashboard', request.url));
      }
    }
  }

  // ðŸ”’ Protection of protected paths
  if (protectedPath) {
    if (!isAuthenticated || !userRole) {
      return NextResponse.redirect(new URL('/auth/login', request.url));
    }

    const requiredRole = protectedPath.role;
    if (Array.isArray(requiredRole)) {
      if (!requiredRole.includes(userRole)) {
        // Check the required role for the protected path.
        return NextResponse.redirect(new URL('/unauthorized', request.url));
      }
    } else {
      if (userRole !== requiredRole) {
        return NextResponse.redirect(new URL('/unauthorized', request.url));
      }
    }
  }

  return NextResponse.next();
}
